import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Modal, Form, message } from "antd";
import { Hideloading, ReloadData, Showloading } from "../../redux/rootSlice";
import axios from "axios";

const AdminAchievements = () => {
  const dispatch = useDispatch();
  const { portfolioData } = useSelector((state) => state.root);
  const { achievements } = portfolioData;
  const [showAddEditModal, setShowAddEditModal] = useState(false);
  const [selectedItemForEdit, setSelectedItemForEdit] = useState(null);
  const [type, setType] = useState("add");
  const onFinish = async (values) => {
    try {
      const tempTechnologies = values?.technolgies?.split(",") || [];
      values.technolgies = tempTechnologies;
      dispatch(Showloading());
      let response;
      if (selectedItemForEdit) {
        response = await axios.post("/api/portfolio/update-achievement", {
          ...values,
          _id: selectedItemForEdit._id,
        });
      } else {
        response = await axios.post("/api/portfolio/add-achievement", values);
      }
      dispatch(Hideloading());
      if (response.data.success) {
        message.success(response.data.message);
        setShowAddEditModal(false);
        setSelectedItemForEdit(null);
        dispatch(Hideloading());
        dispatch(ReloadData(true));
      } else {
        message.error(response.data.message);
      }
    } catch (error) {
      dispatch(Hideloading());
      message.error(error.message);
    }
  };

  const onDelete = async (item) => {
    try {
      dispatch(Showloading());
      const response = await axios.post("/api/portfolio/delete-achievement", {
        _id: item._id,
      });
      dispatch(Hideloading());
      if (response.data.success) {
        message.success(response.data.message);
        dispatch(Hideloading());
        dispatch(ReloadData(true));
      } else {
        message.error(response.data.message);
      }
    } catch (error) {
      dispatch(Hideloading());
      message.error(error.message);
    }
  };

  return (
    <div>
      <div className="flex justify-end">
        <button
          className="bg-primary px-5 text-white py-2"
          onClick={() => {
            setSelectedItemForEdit(null);
            setShowAddEditModal(true);
          }}
        >
          Add Achievements
        </button>
      </div>
      <div className="grid grid-cols-3 gap-5 mt-5 sm:grid-cols-1 ">
        {achievements.map((achievement) => (
          <div className="overflow-hidden border shadow border-gray-400 p-5 gap-5 flex flex-col">
            <h1 className="text-primary text-xl font-bold text-center">
              {achievement.title}
            </h1>
            <hr />
            <img src={achievement.image} alt="" className="h-60 w-80 m-auto rounded" />
            <h1>Role : {achievement.title}</h1>
            <h1>
              {achievement.description.length > 100
                ? `${achievement.description.substring(0, 100)}...`
                : achievement.description}
            </h1>
            <a
              target="_blank"
              rel="noopener noreferrer" // Add this line to address the security risk
              href={achievement.link}
              className="text-blue-500 hover:text-blue-700 underline"
            >
              <h1>Project Link</h1>
            </a>
            <div
              className={`${
                achievement.description.length > 100
                  ? "flex  justify-end gap-4 "
                  : "flex  justify-end gap-5 my-10"
              }`}
            >
              <button
                className="bg-secondary text-white px-5 py-2"
                onClick={() => {
                  onDelete(achievement);
                }}
              >
                Delete
              </button>
              <button
                className="bg-primary text-white px-5 py-2"
                onClick={() => {
                  setSelectedItemForEdit(achievement);
                  setShowAddEditModal(true);
                  setType("edit");
                }}
              >
                Edit
              </button>
            </div>
          </div>
        ))}
      </div>

      {(type === "add" || selectedItemForEdit) && (
        <Modal
          visible={showAddEditModal}
          title={selectedItemForEdit ? "Edit Achievement" : "Add Achievement"}
          onCancel={() => {
            setShowAddEditModal(false);
            setSelectedItemForEdit(null);
          }}
          footer={null}
        >
          <Form
            layout="vertical"
            onFinish={onFinish}
            initialValues={{
              ...selectedItemForEdit,
              technolgies: selectedItemForEdit?.technolgies?.join(" ,"),
            }}
          >
            <Form.Item name="title" label="Title">
              <input placeholder="Title" className="projInput" />
            </Form.Item>
            <Form.Item name="description" label="Description">
              <input placeholder="Description" className="projInput" />
            </Form.Item>
            <Form.Item name="image" label="Image">
              <textarea placeholder="Image" className="projInput" />
            </Form.Item>
            <Form.Item name="link" label="Link">
              <input placeholder="Link" className="projInput" />
            </Form.Item>

            <div className="flex">
              <button
                className="border-primary text-primary px-5 py-2"
                onClick={() => {
                  setShowAddEditModal(false);
                }}
              >
                Cancel
              </button>
              <button className="bg-primary text-white px-5 py-2">
                {selectedItemForEdit ? "Update" : "Add"}
              </button>
            </div>
          </Form>
        </Modal>
      )}
    </div>
  );
};

export default AdminAchievements;
