import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Modal, Form, message } from "antd";
import { Hideloading, ReloadData, Showloading } from "../../redux/rootSlice";
import axios from "axios";

const Experiences = () => {
  const dispatch = useDispatch();
  const { portfolioData } = useSelector((state) => state.root);
  const { experiences } = portfolioData;
  const [showAddEditModal, setShowAddEditModal] = useState(false);
  const [selectedItemForEdit, setSelectedItemForEdit] = useState(null);
  const [type, setType] = useState("add");
  const onFinish = async (values) => {
    try {
      dispatch(Showloading());
      let response;
      if (selectedItemForEdit) {
        response = await axios.post("/api/portfolio/update-experience", {
          ...values,
          _id: selectedItemForEdit._id,
        });
      } else {
        response = await axios.post("/api/portfolio/add-experience", values);
      }
      dispatch(Hideloading());
      if (response.data.success) {
        message.success(response.data.message);
        setShowAddEditModal(false);
        setSelectedItemForEdit(null)
        dispatch(Hideloading());
        dispatch(ReloadData(true));
      } else {
        message.error(response.data.message);
      }
    } catch (error) {
      dispatch(Hideloading());
      message.error(error.message);
    }
  };

  const onDelete = async(item)=>{
    try {
      dispatch(Showloading())
      const response = await axios.post("/api/portfolio/delete-experience",{
        _id:item._id,
      })
      dispatch(Hideloading());
      if(response.data.success){
        message.success(response.data.message)
        dispatch(Hideloading())
        dispatch(ReloadData(true))
      }else{
        message.error(response.data.message)
      }
    } catch (error) {
      dispatch(Hideloading())
      message.error(error.message)
    }
  }

  return (
    <div>
      <div className="flex justify-end">
        <button
          className="bg-primary px-5 text-white py-2"
          onClick={() => {
            setSelectedItemForEdit(null);
            setShowAddEditModal(true);
          }}
        >
          Add Experience
        </button>
      </div>
      <div className="grid grid-cols-4 gap-5 mt-5 sm:grid-cols-1">
        {experiences.map((experience) => (
          <div className="overflow-hidden border shadow border-gray-400 p-5 gap-2 flex flex-col">
            <h1 className="text-primary text-xl font-bold">
              {experience.period}
            </h1>
            <hr />
            <h1 className="mt-2">Company : {experience.company}</h1>
            <h1>Role : {experience.title}</h1>
            <h1>
              {experience.description.length > 100
                ? `${experience.description.substring(0, 100)}...`
                : experience.description}
            </h1>
            <div
              className={`${
                experience.description.length > 100
                  ? "flex  justify-end gap-4 "
                  : "flex  justify-end gap-5 my-10"
              }`}
            >
              <button className="bg-secondary text-white px-5 py-2"
              onClick={()=>{
                onDelete(experience)
              }}>
                Delete
              </button>
              <button
                className="bg-primary text-white px-5 py-2"
                onClick={() => {
                  setSelectedItemForEdit(experience);
                  setShowAddEditModal(true);
                  setType("edit");
                }}
              >
                Edit
              </button>
            </div>
          </div>
        ))}
      </div>

      {(type === "add" || selectedItemForEdit) && (
        <Modal
          visible={showAddEditModal}
          title={selectedItemForEdit ? "Edit Experence" : "Add Experience"}
          onCancel={() => {
            setShowAddEditModal(false);
            setSelectedItemForEdit(null)
          }}
          footer={null}
        >
          <Form
            layout="vertical"
            onFinish={onFinish}
            initialValues={selectedItemForEdit}
          >
            <Form.Item name="period" label="Period">
              <input placeholder="Period" className="expInput" />
            </Form.Item>
            <Form.Item name="company" label="Company">
              <input placeholder="Company" className="expInput" />
            </Form.Item>
            <Form.Item name="title" label="Title">
              <input placeholder="Title" className="expInput" />
            </Form.Item>
            <Form.Item name="description" label="Description">
              <input placeholder="Description" className="expInput" />
            </Form.Item>

            <div className="flex">
              <button
                className="border-primary text-primary px-5 py-2"
                onClick={() => {
                  setShowAddEditModal(false);
                }}
              >
                Cancel
              </button>
              <button className="bg-primary text-white px-5 py-2">
                {selectedItemForEdit ? "Update" : "Add"}
              </button>
            </div>
          </Form>
        </Modal>
      )}
    </div>
  );
};

export default Experiences;
