import React from "react";
import SectionTitles from "../../components/SectionTitles";
import { useState } from "react";
import { useSelector } from "react-redux";

const Projects = () => {
  const [selectedItemIndex, setSelectedItemIndex] = useState(0);
  const { portfolioData } = useSelector((state) => state.root);
  const { project } = portfolioData;
  return (
    <div>
      <SectionTitles title={"Projects"} />

      <div className="flex py-10 gap-20 sm:flex-col">
        <div className="flex flex-col gap-5 border-l-2 border-[#135e4c82] w-1/3 sm:flex-row sm:overflow-x-scroll sm:w-full">
          {project.map((project, index) => (
            <div
              className="cursor-pointer"
              key={index}
              onClick={() => {
                setSelectedItemIndex(index);
              }}
            >
              <h1
                className={`text-xl px-5 ${
                  selectedItemIndex === index
                    ? "text-tertiary border-tertiary border-l-4 -ml-[2px] bg-[#1a7f5a31] py-3"
                    : "text-white"
                }`}
              >
                {project.title}
              </h1>
            </div>
          ))}
        </div>

        <div className="flex items-center justify-center gap-10 sm:flex-col ">
          <img
            src={project[selectedItemIndex].image}
            className="h-60 w-72"
            alt=""
          />
          <div className="flex flex-col gap-5">
            <h1 className="text-secondary text-xl">
              {project[selectedItemIndex].title}
            </h1>
            <p className="text-white">
              {project[selectedItemIndex].description}
            </p>
            <p className="text-white">
              {project[selectedItemIndex].description}
            </p>
            <h1>
              <a
                href={project[selectedItemIndex].link}
                className="inline-block border-2 border-tertiary text-tertiary px-10 py-2 rounded"
                target="_blank"
                rel="noopener noreferrer"
              >
                Code
              </a>
            </h1>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Projects;
