import React from "react";
import { Form, message } from "antd";
import { useSelector,useDispatch } from "react-redux";
import {Showloading,Hideloading} from '../../redux/rootSlice'
import axios from 'axios'

const AdminAbout = () => {
  const { portfolioData } = useSelector((state) => state.root);
  const dispatch = useDispatch()
  const onFinish = async(values) => {
    try{
      const tempSkills = values.skills.split(",");
      values.skills = tempSkills
      dispatch(Showloading())
      const response = await axios.post("/api/portfolio/update-about",{
        ...values,
        _id: portfolioData.about._id,
      })
      dispatch(Hideloading())
      if (response.data.success){
        message.success(response.data.message);
      }else{
        message.error(response.data.message)
      }
    }catch (error){
      dispatch(Hideloading())
      message.error(error.message)
    }
  };

  return (
    <div>
      <Form
        onFinish={onFinish}
        layout="vertical"
        initialValues={{
          ...portfolioData.about,
          skills: portfolioData.about.skills.map(skill => skill.trim()).join(", "),
        }}
      >
        <Form.Item name="lottieURL" label="Lotti URL">
          <input placeholder="Lotti URL" className="adminInput" />
        </Form.Item>
        <Form.Item name="description1" label="Description1">
          <input placeholder="Description1" className="adminInput" />
        </Form.Item>
        <Form.Item name="description2" label="Description2">
          <input placeholder="Description2" className="adminInput" />
        </Form.Item>
        <Form.Item name="skills" label="Skills">
          <input placeholder="Skills" className="adminInput" />
        </Form.Item>
        <div className="flex justify-end w-full">
          <button className="bg-primary text-white px-10 py-2" type='submit' >Save</button>
        </div>
      </Form>
    </div>
  );
};

export default AdminAbout;