import React from "react";

const LeftSider = () => {
  return (
    <div className="fixed left-0 bottom-0 px-10 sm:static">
      <div className="flex flex-col items-center gap-3">
        <div className="flex flex-col sm:flex-row gap-5 py-3">
          <i className="ri-facebook-circle-fill text-gray-400 "></i>
          <i className="ri-linkedin-box-fill text-gray-400 "></i>
          <i className="ri-mail-line text-gray-400 "></i>
          <i className="ri-instagram-line text-gray-400 "></i>
          <a href="https://github.com/modamaan" target="_blank" rel="noreferrer">
            <i className="ri-github-fill text-gray-400 "></i>
          </a>
        </div>
        <div className="w-[1px] h-32 bg-[#125f63] sm:hidden"></div>
      </div>
    </div>
  );
};

export default LeftSider;
