import React, { useEffect } from 'react'
import Header from '../../components/Header'
import { Tabs } from 'antd';
import AdminIntro from './AdminIntro';
import AdminAbout from './AdminAbout';
import { useSelector } from 'react-redux';
import AdminExperiences from './AdminExperiences'
import AdminProjects from './AdminProjects';
import AdminAchievements from './AdminAchievements';
import AdminContact from './AdminContact';
const Admin = () => {
    const { portfolioData } = useSelector((state) => state.root);

    useEffect(() => {
        if (!localStorage.getItem('token')) {
            window.location.href = "/admin-login"
        }
    })
    const items = [
        {
            key: '1',
            label: 'Intro',
            children: <AdminIntro />,
        },
        {
            key: '2',
            label: 'About',
            children: <AdminAbout />,
        },
        {
            key: '3',
            label: 'Experiences',
            children: <AdminExperiences />,
        },
        {
            key: '4',
            label: 'Projects',
            children: <AdminProjects />,
        },
        {
            key: '5',
            label: 'Achievements',
            children: <AdminAchievements />,
        },
        {
            key: '6',
            label: 'Contacts',
            children: <AdminContact />,
        },
    ];
    const App = () => <Tabs defaultActiveKey="1" items={items} className=' p-5 font-bold pb-10' />;
    return (
        <div>
            <Header />
            <div className='flex gap-10 items-center px-5  justify-between' >
                <div><h1 className='text-2xl px-10 py-4' >Portfolio Admin</h1></div>
                <h1 className='underline text-primary text-xl cursor-pointer' onClick={()=>{
                    localStorage.removeItem('token');
                    window.location.href = '/admin-login'
                }} >Logout</h1>
            </div>
            {portfolioData && <App />}
        </div>
    )
}

export default Admin