import React from "react";
import { Form, message } from "antd";
import { useSelector,useDispatch } from "react-redux";
import {Showloading,Hideloading} from '../../redux/rootSlice'
import axios from 'axios'


const AdminIntro = () => {
  const { portfolioData } = useSelector((state) => state.root);
  const dispatch = useDispatch()
  const onFinish = async(values) => {
    try{
      dispatch(Showloading())
      const response = await axios.post("/api/portfolio/update-intro",{
        ...values,
        _id: portfolioData.intro._id,
      })
      dispatch(Hideloading())
      if (response.data.success){
        message.success(response.data.message);
      }else{
        message.error(response.data.message)
      }
    }catch (error){
      dispatch(Hideloading())
      message.error(error.message)
    }
  };

  return (
    <div>
      <Form
        onFinish={onFinish}
        layout="vertical"
        initialValues={portfolioData.intro}
      >
        <Form.Item name="welcomeText" label="Welcome Text">
          <input placeholder="Welcome Text" className="adminInput" />
        </Form.Item>
        <Form.Item name="firstName" label="First Name">
          <input placeholder="First Name" className="adminInput" />
        </Form.Item>
        <Form.Item name="lastName" label="Second Name">
          <input placeholder="Second Name" className="adminInput" />
        </Form.Item>
        <Form.Item name="caption" label="Caption">
          <input placeholder="Caption" className="adminInput" />
        </Form.Item>
        <Form.Item name="description" label="Description">
          <textarea placeholder="Description" />
        </Form.Item>
        <div className="flex justify-end w-full">
          <button className="bg-primary text-white px-10 py-2" type='submit' >Save</button>
        </div>
      </Form>
    </div>
  );
};

export default AdminIntro;
