import React from "react";
import SectionTitles from "../../components/SectionTitles";
import { useState } from "react";
import { useSelector } from "react-redux";

const Achievements = () => {
  const [selectedItemIndex, setSelectedItemIndex] = useState(0);
  const { portfolioData } = useSelector((state) => state.root);
  const { achievements } = portfolioData;
  return (
    <div>
      <SectionTitles title={"Achievements"} />

      <div className="flex py-10 gap-20 sm:flex-col">
        <div className="flex flex-col gap-5 border-l-2 border-[#135e4c82] w-1/3 sm:flex-row sm:overflow-x-scroll sm:w-full">
          {achievements.map((achievement, index) => (
            <div
              className="cursor-pointer"
              key={index}
              onClick={() => {
                setSelectedItemIndex(index);
              }}
            >
              <h1
                className={`text-xl px-5 ${
                  selectedItemIndex === index
                    ? "text-tertiary border-tertiary border-l-4 -ml-[2px] bg-[#1a7f5a31] py-3"
                    : "text-white"
                }`}
              >
                {achievement.title}
              </h1>
            </div>
          ))}
        </div>

        <div className="flex items-center justify-center gap-10 sm:flex-col ">
          <div className="flex flex-col gap-5">
            <h1 className="text-secondary text-xl">
              {achievements[selectedItemIndex].title}
            </h1>
            <p className="text-white">
              {achievements[selectedItemIndex].description}
            </p>
            <p className="text-white">
              Lorem ipsum dolor sit, amet consectetur adipisicing elit. Quaerat
              modi placeat cum distinctio nam fugit perferendis doloremque
              assumenda, cupiditate, blanditiis quae aut ut, ullam quam magnam
              in explicabo magni quidem.
            </p>
            <h1>
              <a
                href={achievements[selectedItemIndex].link}
                className="inline-block border-2 border-tertiary text-tertiary px-10 py-2 rounded"
                target="_blank"
                rel="noopener noreferrer"
              >
                View
              </a>
            </h1>
          </div>
          <img
            src={achievements[selectedItemIndex].image}
            className="h-52 w-80"
            alt=""
          />
        </div>
      </div>
    </div>
  );
};

export default Achievements;
