import React from "react";
import { Form, message } from "antd";
import { useSelector,useDispatch } from "react-redux";
import {Showloading,Hideloading} from '../../redux/rootSlice'
import axios from 'axios'


const AdminContact = () => {
  const { portfolioData } = useSelector((state) => state.root);
  const dispatch = useDispatch()
  const onFinish = async(values) => {
    try{
      dispatch(Showloading())
      const response = await axios.post("/api/portfolio/update-contact",{
        ...values,
        _id: portfolioData.contact._id,
      })
      dispatch(Hideloading())
      if (response.data.success){
        message.success(response.data.message);
      }else{
        message.error(response.data.message)
      }
    }catch (error){
      dispatch(Hideloading())
      message.error(error.message)
    }
  };

  return (
    <div>
      <Form
        onFinish={onFinish}
        layout="vertical"
        initialValues={portfolioData.contact}
      >
        <Form.Item name="name" label="Name">
              <input placeholder="Name" className="projInput" />
            </Form.Item>
            <Form.Item name="gender" label="Gender">
              <input placeholder="Gender" className="projInput" />
            </Form.Item>
            <Form.Item name="email" label="Email">
              <textarea placeholder="Email" className="projInput" />
            </Form.Item>
            <Form.Item name="mobile" label="Mobile">
              <input placeholder="Mobile No" className="projInput" />
            </Form.Item>
            <Form.Item name="age" label="Age">
              <input placeholder="Age" className="projInput" />
            </Form.Item>
            <Form.Item name="address" label="Address">
              <input placeholder="Address" className="projInput" />
            </Form.Item>
            <Form.Item name="country" label="Country">
              <input placeholder="Country" className="projInput" />
            </Form.Item>
        <div className="flex justify-end w-full">
          <button className="bg-primary text-white px-10 py-2" type='submit' >Save</button>
        </div>
      </Form>
    </div>
  );
};

export default AdminContact;
